
import Vue from 'vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { getImgActivityList } from '@/api/detail'
export default {
name: 'shoppingCard',
components: {
Radio,
Group,
Swiper,
SwiperItem
},
data () {
return {
bannerlist:[],
activitylist:[],
id:''
}
},
created () {
this.getlist()
},
methods: {
getlist(){
getImgActivityList()
.then(res => {
if (res.data.errorcode === 0) {
res.data.data.bannerlist.map((item, index) => {
let listurl = {}
listurl.img = item.picurl
this.bannerlist.push(item.picurl)
})

this.activitylist = res.data.data.activitylist

} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
goall(id){
this.$router.push({path:"/showphoto/actdetail",query:{"id":id}})
}
},
mounted(){
const that = this
const usertitle = '晒图征集活动'
const subtitle = ''
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: usertitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: usertitle,
desc: subtitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



